"use strict";
import { formattingSettings } from "powerbi-visuals-utils-formattingmodel";
var FormattingSettingsCard = formattingSettings.Card;
var FormattingSettingsModel = formattingSettings.Model;
class PasswordSettingsCard extends FormattingSettingsCard {
    constructor() {
        super(...arguments);
        this.password = new formattingSettings.TextInput({
            name: "password",
            displayName: "Password",
            value: "",
            placeholder: ""
        });
        this.showPassword = new formattingSettings.ToggleSwitch({
            name: "showPassword",
            displayName: "Show Password",
            value: false
        });
        this.name = "passwordSettings";
        this.displayName = "Password Settings";
        this.slices = [this.password, this.showPassword];
    }
}
class FilterSettingsCard extends FormattingSettingsCard {
    constructor() {
        super(...arguments);
        this.organizationMapping = new formattingSettings.TextInput({
            name: "organizationMapping",
            displayName: "Organization Password Mapping",
            value: JSON.stringify({
                "FAO123": "FAO",
                "UNICEF123": "UNICEF",
                "UNHCR123": "UNHCR",
                "WHO123": "WHO",
                "WIPO123": "WIPO"
            }, null, 2),
            placeholder: '{"password1":"FAO","password2":"UNICEF"}'
        });
        this.name = "filterSettings";
        this.displayName = "Filter Settings";
        this.slices = [this.organizationMapping];
    }
}
export class VisualFormattingSettingsModel extends FormattingSettingsModel {
    constructor() {
        super(...arguments);
        this.passwordSettings = new PasswordSettingsCard();
        this.filterSettings = new FilterSettingsCard();
        this.cards = [this.passwordSettings, this.filterSettings];
    }
}
